import 'bulma/css/bulma.css';
import './App.scss';

import { useState } from 'react';
import cn from 'classnames';

export const goods = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

export const App = () => {
  const [selectedGood, setSelectedGood] = useState('Jam');

  const isCurrentGood = good => (good === selectedGood);

  return (
    <main className="section container">
      <h1 className="title is-flex is-align-items-center">
        {!selectedGood
          ? 'No goods selected'
          : (
            <>
              {`${selectedGood} is selected`}

              <button
                data-cy="ClearButton"
                type="button"
                className="delete ml-3"
                onClick={() => {
                  setSelectedGood('');
                }}
              />
            </>
          )
        }
      </h1>

      <table className="table">
        <tbody>
          {goods.map(good => (
            <tr
              data-cy="Good"
              key={good}
              className={cn('', {
                'has-background-success-light': isCurrentGood(good),
              })}
            >
              <td>
                <button
                  data-cy={cn('', {
                    AddButton: !isCurrentGood(good),
                    RemoveButton: isCurrentGood(good),
                  })}
                  type="button"
                  className={cn('button', {
                    ' is-info': isCurrentGood(good),
                  })}
                  onClick={() => {
                    setSelectedGood(
                      isCurrentGood(good)
                        ? ''
                        : good,
                    );
                  }}
                >
                  {isCurrentGood(good)
                    ? '-'
                    : '+'
                  }
                </button>
              </td>

              <td data-cy="GoodTitle" className="is-vcentered">
                {good}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </main>
  );
};
